<docs>
  # SoundButton
</docs>

<script lang="ts">
import SoundOffSVG from "assets/svg/sound_off.svg?raw";
import SoundOnSVG from "assets/svg/sound_on.svg?raw";
import { defineComponent, ref, toRefs } from 'vue';

export default defineComponent({
  name: 'SoundButton',
  props: {
    theme: {
      type: String,
      default: 'university',
    },
  },
  setup(props) {

    const mute = ref((Howler as any)._muted);

    const toggleSound = () => {
      mute.value = !mute.value;
      Howler.mute(mute.value);
    }

    return {
     ...toRefs(props),
     mute,
     SoundOffSVG,
     SoundOnSVG,
     toggleSound,
    };
  },
  
});
</script>
  
<template>
  <div class="SoundButton">
    <ui-button class="btn"
               theme="icon"
               :variant="theme"
               :icon-selected="SoundOffSVG"
               :icon="SoundOnSVG"
               :selected="mute"
               :data-mute="mute"
               :highlight="false"
               @click="toggleSound"
    />
  </div>
</template>

<style lang="stylus" scoped>

  .SoundButton
   absolute bottom 10px left 10px
   z-index 1
   width 40px
   trans(opacity, 1s)

  &[data-mute="false"]
    :deep()
      .line1 
        transform-origin center center
        animation: soundWave 1.5s infinite ease-in-out alternate;

      .line2
        transform-origin center center
        animation: soundWave 1s infinite ease-in-out alternate 0.2s;

      .line3
        transform-origin center center
        animation: soundWave 1.5s infinite ease-in-out alternate 0.4s;      

      .line4
        transform-origin center center
        animation: soundWave 1s infinite ease-in-out alternate 0.6s;

   &[data-hidden="true"]
     opacity 0
     pointer-events none

@keyframes soundWave
  0%
    transform scaleY(.8)
  50%
    transform scaleY(1.25)
  100%
    transform scaleY(.8)
</style>
